.medicx-team-member-img{
    position: relative;
    display: block;
}
.medicx-team-member-wrap:hover .medicx-team-member-img::before{
    opacity: 1;
}
.medicx-team-member-img:hover::before {
    opacity: 1;
}
.medicx-team-member-img::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    transition: .3s;
    background: linear-gradient(180deg, rgba(3, 3, 3, 0) 32.5%, rgba(3, 3, 3, 0.8) 100%);
}


.medicx-team-member-wrap:hover .medicx-team-member-social-icon ul li a {
    opacity: 1;
}
.medicx-team-member-social-icon ul li a {
    position: absolute;
    transform: translate(-50%, -50%);
    bottom: 30%;
    left: 50%;
    gap: 24px;
    display: flex;
    justify-content: center;
    opacity: 0;
    transition: .4s;
}


.medicx-team-member{
    &-wrap{
        border-radius: 17px;
        transition: all 0.4s;
        box-shadow: 0px 4px 80px 0px #00000012;
        border: 1px solid var(--light-color);
        margin-bottom: 24px;
        &:hover{
            border: none;
        }
    }
    &-img {
        img{
            width: 100%;
        }
    }
    &-content{
        padding: 30px;
        h5{
            margin-bottom: 5px;
        }
    }
    &-social-icon{
        text-align: center;
        transition: all 0.4s;
        ul{
            li{
                display: inline-block;
                margin-right: 24px;
                a{
                    width: 50px;
                    height: 50px;
                    border-radius: 100px;
                    border: 1px solid var(--white-color);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: #000;
                    &:hover{
                        background: var(--accent-color);
                        border: transparent;
                    }
                }
            }
        }
    }
    &-aboutus{
        margin-top: 120px;
        @include max (991px){
            margin-top: 0;
        }
    }
    
}
.team-member{
    &-content{
        margin-bottom: 40px;
    }
    h2{
        margin-bottom: 15px!important;
    }
}
.team-member-details{
    &-wrap{
        display: flex;
        gap: 40px;

        @include max(767px){
            width: 100%;
            gap: 20px;
        }
    }
    &-data{
        h4{
            margin-bottom: 24px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}
.member-social-contuct{
    &-wrap {
        display: flex;
        gap: 25px;
        margin-top: 40px;
        @include max(767px){
            margin-top: 25px;
        }
    }
    &-data{
        display: flex;
        align-items: center;
    }
}
.medicx-member{
    &-experience{
        margin-top: 80px;
        @include max (991px){
            margin-top: 60px;
        }
        @include max (991px){
            margin-top: 40px;
        }
        h3{
            margin-bottom: 30px;
        }
    }
}
.our-teaminner {
    margin-left: 0!important;
}

.medicx-member{
    &-skill-wrap{
        margin-left: 60px;
        @include max (991px){
            margin-left: 0;
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
    }
    &-bar{
        margin-bottom: 10px;
    }
    &-progress-line{
        position: relative;
        height: 10px;
        width: 550px;
        background: var(--light-color);
        border-radius: 10px;
        transform: scaleX(0);
        transform-origin: left;
        animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0;
        }
        @include max (767px){
            width: 100%;
        }
        h5{
            position: absolute;
            background: var(--accent-color);
            height: 100%;
            border-radius: 10px;
            transform: scaleX(0);
            transform-origin: left;
            animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
            
        }
        h5::before{
            position: absolute;
            content: "";
            height: 0;
            right: 0;
            top: -14px;
            width: 0;
            border: 7px solid transparent;
            border-bottom-width: 0px;
            border-right-width: 0px;
            border-top-style: #f0f0f0;
        }
        h5::after{
            position: absolute;
            right: -32px;
            top: -30px;
            color: var(--dark-bg);
            font-size: 20px;
            font-weight: 700;
            padding: 1px 8px;
            border-radius: 3px;
        }
        .html{
            width: 87%;
        }
        .html::after{
            content: "87%";
        }
        .css{
            width: 90%;
        }
        .css::after{
            content: "90%";
        }
        .js{
            width: 60%;
        }
        .js::after{
            content: "60%";
        }
    }
    h3{
        margin-bottom: 30px;
    }
}
@keyframes animate {
    100% {
    transform: scaleX(1);
    }
}
  
  

