

// strat medicx project


.medicx-hero-section {
    padding-top: 140px;
}
.medicx-hero-item{
    width: 100%;
    display: inline-block;
    padding: 150px 0 100px;
  }
.medicx-hero-slider-content {
    max-width: 900px;
    p{
        font-size: 24px;
        line-height: 36px;
    }
}
.medicx-left-arrowbtn {
    position: absolute;
    top: 55%;
    left: 5%;
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 100px;
    img{
        position: absolute;
        top: 22%;
        right: 40%;
        width: 15px;
        cursor: pointer;
    }
}
.medicx-right-arrowbtn {
    position: absolute;
    top: 55%;
    right: 5%;
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 100px;
    img{
        position: absolute;
        top: 24%;
        right: 30%;
        width: 15px;
        cursor: pointer;
    }
}