


.medicx-iconbox{
    &-wrap {
        border-radius: 10px;
        background-color: var(--white-color);
        padding: 30px;
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            background: var(--accent-color);
            h4,p,a{
                color: var(--white-color);
            }
            .medicx-iconbox-icon{
                background: var(--white-color);
            }
            svg path{
                fill: var(--accent-color);
            }
            .medicx-iconbox-data{
                a{
                    svg path{
                        stroke: var(--white-color);
                    }
                }
            }
        }
    }
    &-icon {
        margin-bottom: 24px;
        width: 80px;
        height: 80px;
        border-radius: 100px;
        background: var(--accent-color);
        position: relative;
        transition: all .4s;
        svg{
            transition: all .4s;
            position: absolute;
            top: 10px;
            left: 18px;
        }
    }
    &-data {
        h4 {
            font-size: 34px;
            transition: all 0.4s;
            margin-bottom: 15px;
        }
        p{
            transition: all 0.4s;

        }
        a{
            color: var(--accent-color);
            font-weight: 700;
            svg{
                transition: all .4s;
                margin-left: 10px;
            }
        }
       
    }
    // end icon box one v1
    &-wrap2 {
        display: flex;
        gap: 12px;
        margin-bottom: 24px;
        text-align: left;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &-icon2 {
        flex-shrink: 0;
    }
    &-data2 {
        h5 {
            margin-bottom: 5px;
        }
        h4 {
            margin-bottom: 15px;
        }
        a {
            color: var(--white-color);
            font-size: 20px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}
// medicx v6

.medicx-list-icon-iconv6{
    ul{
        li{
            font-size: 23px;
            font-weight: 700;
            line-height: 32px;
            text-align: left;
            color: #0e0e0e;
            margin-bottom: 16px;
            img{
                margin-right: 15px;
            }
        }
    }
}
