.medicx-blog{
    &-wrap {
        border: 1px solid var(--light-color);
        border-radius: 12px;
        margin-bottom: 24px;
        &.active{
            box-shadow: 0px 4px 80px 0px #00000012!important;
            border: none;
        }
    }
    &-img{
        img{
            width: 100%;
        }
    }
    &-content{
        padding: 30px 17px;
        h5{
            margin-bottom: 30px;
        }
    }
    &-icon {
        display: flex;
        gap: 10px;
        align-items: center;
        .market-content{
            margin-top: 3px;
            p, ul li{
                color: var(--body-color);
            }
        }
    }
    &-meta {
        display: flex;
        gap: 15px;
        padding-bottom: 24px;
    }
    &-btn-icon{
        font-size: 16px;
        line-height: 28px;
        font-weight: 700;
        color: var(--heading-color);
        svg{
            margin-left: 10px;
        }
        &:hover{
            color: var(--accent-color)!important;
            svg path{
                stroke: var(--accent-color);
            }
        }
    }
}